/*    - AgreementStep.module.css */

.checkbox {
  margin-left: 15px;  
  display: flex;
  align-items: center;
  margin-bottom: 18px;
  gap: 8px;
  color: #797979;
  font-family: "Apple SD Gothic Neo";
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  animation: checkboxSlideIn 0.5s ease-out backwards;
  transition: all 0.2s ease;
  cursor: pointer;
}

.checkbox:hover {
  transform: translateX(3px);
  color: #5FC24A;
}

.checkbox input[type="checkbox"] {
  width: 18px;
  height: 18px;
  margin: 0;
  cursor: pointer;
  accent-color: #7DDB69;
  transition: all 0.2s ease;
}

.checkbox input[type="checkbox"]:checked {
  transform: scale(1.1);
}

/*     */
.checkboxAll {
  margin-left: 15px;  
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  gap: 8px;
  color: #797979;
  font-family: "Apple SD Gothic Neo";
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  animation: checkboxSlideIn 0.5s ease-out backwards;
  transition: all 0.2s ease;
  cursor: pointer;
}

.checkboxAll:hover {
  color: #7DDB69;
}

.checkboxAll input[type="checkbox"] {
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  accent-color: #7DDB69;
  transition: all 0.2s ease;
}

/*  */
.divider {
  border: none;
  border-top: 1px solid #e3e3e3;
  width: 90%;
  margin-top: -5px;
  margin-bottom: 21px;
  animation: dividerExpand 0.6s ease-out 0.7s backwards;
}

/* /   */
.requiredLabel {
  color: #f44336;
  font-weight: 600;
}

.optionalLabel {
  color: #7DDB69;
  font-weight: 600;
}

/*    */
.termsLink {
  color: #579DB7;
  text-decoration: underline;
  transition: color 0.2s ease;
}

.termsLink:hover {
  color: #456a7a;
}

/*  */
@keyframes checkboxSlideIn {
  0% {
    opacity: 0;
    transform: translateX(-15px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes dividerExpand {
  0% {
    width: 0;
    opacity: 0;
  }
  100% {
    width: 90%;
    opacity: 1;
  }
}

/*     */
.checkbox:nth-child(1) { animation-delay: 0.1s; }
.checkbox:nth-child(2) { animation-delay: 0.2s; }
.checkbox:nth-child(3) { animation-delay: 0.3s; }
.checkbox:nth-child(4) { animation-delay: 0.4s; }
.checkbox:nth-child(5) { animation-delay: 0.5s; }
.checkbox:nth-child(6) { animation-delay: 0.6s; }

/*  */
@media (max-width: 500px) {
  .checkbox {
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .checkboxAll {
    font-size: 15px;
  }
}